.game-level {
  margin-bottom: 20px;
}

.game-level__controls {
  height: 25px;
  margin-bottom: 5px;
}

.game-board {
  background-color: #4f5d75;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  padding: 3px 0;
}

.game-board__tile {
  width: 100px;
  height: 100px;
  margin: 3px;
  -webkit-perspective: 300px;
          perspective: 300px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  background-color: #bfc0c0;
  color: #ffffff;
  font-size: 32px;
  font-family: 'Monoton', cursive;
}

@-webkit-keyframes tileWiggle {
  0%   { -webkit-transform:  rotateZ(0deg); transform:  rotateZ(0deg) }
  25%  { -webkit-transform:  rotateZ(5deg); transform:  rotateZ(5deg) }
  50%  { -webkit-transform:  rotateZ(-5deg); transform:  rotateZ(-5deg) }
  100% { -webkit-transform:  rotateZ(0deg); transform:  rotateZ(0deg) }
}

@keyframes tileWiggle {
  0%   { -webkit-transform:  rotateZ(0deg); transform:  rotateZ(0deg) }
  25%  { -webkit-transform:  rotateZ(5deg); transform:  rotateZ(5deg) }
  50%  { -webkit-transform:  rotateZ(-5deg); transform:  rotateZ(-5deg) }
  100% { -webkit-transform:  rotateZ(0deg); transform:  rotateZ(0deg) }
}

.game-board__tile--wiggling {
  position: relative;
  -webkit-animation-name: tileWiggle;
          animation-name: tileWiggle;
  -webkit-animation-duration: 3s;
          animation-duration: 3s;
}

@-webkit-keyframes tileExplodeUp {
  0%   { top: 0px }
  15%  { top: 100px }
  40%  { top: 1000px }
  100% { top: 10000px }
}

@keyframes tileExplodeUp {
  0%   { top: 0px }
  15%  { top: 100px }
  40%  { top: 1000px }
  100% { top: 10000px }
}

@-webkit-keyframes tileExplodeDown {
  0%   { top: 0px }
  15%  { top: -100px }
  40%  { top: -1000px }
  100% { top: -10000px }
}

@keyframes tileExplodeDown {
  0%   { top: 0px }
  15%  { top: -100px }
  40%  { top: -1000px }
  100% { top: -10000px }
}

.game-board__tile--exploding-up {
  position: relative;
  -webkit-animation-name: tileExplodeUp;
          animation-name: tileExplodeUp;
  -webkit-animation-duration: 3s;
          animation-duration: 3s;
}

.game-board__tile--exploding-down {
  position: relative;
  -webkit-animation-name: tileExplodeDown;
          animation-name: tileExplodeDown;
  -webkit-animation-duration: 3s;
          animation-duration: 3s;
}



